<template>
  <div v-if="!$vuetify.breakpoint.smAndDown">
    <div @click="mostrarMenu">
      <item-opcion :texto="fechaParaMostrar" :imagen="require('../../assets/calendario.png')" descripcion="Fecha"/>
    </div>

    <v-menu v-model="menuEscritorio" :close-on-content-click="false" absolute offset-y :position-x="x" :position-y="y">
      <v-date-picker :value="value" @input="menuEscritorio = false" :allowed-dates="allowedDates" @change="guardarFecha"
                     locale="es"/>
    </v-menu>
  </div>

  <v-dialog v-else ref="dialog" v-model="dialogMovil" :return-value.sync="fechaEnDialog" persistent width="290px">
    <template v-slot:activator="{ on, attrs }">
      <v-btn v-bind="attrs" v-on="on">
        <span> {{ fechaParaMostrar }} </span>

        <img :src="require('../../assets/calendario.png')" width="30"/>
      </v-btn>
    </template>
    <v-date-picker v-model="fechaEnDialog" scrollable :allowed-dates="allowedDates" locale="es">
      <v-spacer />
      <v-btn text color="primary" @click="dialogMovil = false">Cancelar</v-btn>
      <v-btn text color="primary" @click="getFechaDialog">Aceptar</v-btn>
    </v-date-picker>
  </v-dialog>
</template>

<script>
  import FechaService from "../../services/FechaService";
  import ItemOpcion from "./PanelDeOpciones/ItemOpcion";

  export default {
    name: "SelectFecha",
    components: {ItemOpcion},
    props: {
      value: String
    },
    data () {
      return {
        fechaEnDialog: this.value,
        dialogMovil: false,
        menuEscritorio: false,
        x: null,
        y: null
      }
    },
    watch: {
      value (val) {
        this.fechaEnDialog = val
      }
    },
    computed: {
      fechaParaMostrar: (vm) => FechaService.getFechaString(vm.value, 'DD MMMM YYYY')
    },
    methods: {
      allowedDates: val => FechaService.enRangoDentroDeSemanaActual(val),
      guardarFecha (fecha) {
        this.$emit('input', fecha)
      },
      getFechaDialog () {
        this.$refs.dialog.save(this.fechaEnDialog);
        this.guardarFecha(this.fechaEnDialog);
      },
      mostrarMenu (e) {
        e.preventDefault()
        this.menuEscritorio  = false
        this.x = e.clientX
        this.y = e.clientY
        this.$nextTick(() => {
          this.menuEscritorio = true
        })
      }
    }
  }
</script>

<style scoped>

</style>
